@font-face {
  font-family: 'Gilroy';
  src: url("../fonts/Gilroy-Regular.woff") format('woff'), url("../fonts/Gilroy-Regular.woff2") format('woff2'), url("../fonts/Gilroy-Regular.ttf") format('truetype');
  font-weight: normal;
  font-style: normal;
}
@font-face {
  font-family: 'Gilroy-Bold';
  src: url("../fonts/Gilroy-Bold.woff") format('woff'), url("../fonts/Gilroy-Bold.woff2") format('woff2'), url("../fonts/Gilroy-Bold.ttf") format('truetype');
  font-weight: normal;
  font-style: normal;
}
html,
body {
  scrollbar-color: #00afb0 #000; /* «цвет ползунка» «цвет полосы скроллбара» */
  scrollbar-width: thin /* толщина */;
}
/* полоса прокрутки (скроллбар) */
::-webkit-scrollbar {
  width: 8px;
  height: 8px;
  background-color: #000;
}
/* ползунок скроллбара */
::-webkit-scrollbar-thumb {
  background: #00afb0;
}
::-webkit-scrollbar-thumb:hover {
  background: #069e9f;
}
/* Стрелки */
::-webkit-scrollbar-button:vertical:start:decrement {
  background-color: #000;
}
::-webkit-scrollbar-button:vertical:end:increment {
  background-color: #000;
}
::-webkit-scrollbar-button:horizontal:start:decrement {
  background-color: #000;
}
::-webkit-scrollbar-button:horizontal:end:increment {
  background-color: #000;
}
* {
